import { Component, Input, OnInit } from '@angular/core';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { IOptionsPsEditNicknameExposed } from './ps-edit-nickname.component.interfaces';

@Component({
  selector: 'ps-edit-nickname',
  templateUrl: './ps-edit-nickname.component.html',
  styleUrls: ['./ps-edit-nickname.component.scss'],
})
export class PsEditNicknameComponent extends PsBaseFieldComponent implements OnInit {

  @Input() options: IOptionsPsEditNicknameExposed;
  defaultOptions: IOptionsPsEditNicknameExposed = {}
  public editRequestMap = new Map<string, string>();

  constructor(
    common: PsCommonService,
    logger: LoggerService
  ) {
    super(common, logger);
  }

  ngOnInit() {
    this.defaultOptions.newValueFcname = 'newNickName';
    this.defaultOptions.editRequestMap = new Map().set(this.defaultOptions.newValueFcname, this.options.fcName);
    this.defaultOptions.actionUrl = PsCommonBusinessSettings.serviceUrl.updateUserInfoProfile;

    this.common.copyObject(this.defaultOptions, this.options, false);
  }

  onNicknameChange(event) {
    this.commonProv.session.append(CommonBussinessConstant.USER_NICKNAME, event);
  }
}
